// MessageDetail.js -  ()
import React, { useState, useRef, useEffect } from 'react';
import { Search, X, Code, FileText, Eye, Terminal, BookOpen } from 'lucide-react';
import ReactMarkdown from 'react-markdown';
import { Prism as SyntaxHighlighter } from 'react-syntax-highlighter';
import { vscDarkPlus } from 'react-syntax-highlighter/dist/esm/styles/prism';
import remarkGfm from 'remark-gfm';

// : 
const ContentView = ({ message, searchQuery, onHighlight }) => {
  const containerRef = useRef(null);
  
  // 
  const customRenderers = {
    h2: ({node, children}) => (
      <h2 className="text-xl font-bold text-blue-700 border-b border-blue-200 pb-1 mt-5 mb-3">{children}</h2>
    ),
    h3: ({node, children}) => (
      <h3 className="text-lg font-semibold text-blue-600 mt-4 mb-2">{children}</h3>
    )
  };
  
  return (
    <div ref={containerRef} className="markdown-content">
      {message.sender === 'assistant' && message.tools && message.tools.length > 0 && (
        <div className="text-blue-600 mb-3">
          [ {message.tools.length} : {message.tools.map(t => t.name).join(', ')}]
          
          {message.tools.map((tool, idx) => (
            <div key={idx} className="ml-4 mt-2 mb-3 text-sm">
              <div className="font-medium"> {idx + 1}: {tool.name}</div>
              <div className="border-t border-gray-200 my-1"></div>
              
              {tool.input && typeof tool.input === 'object' && (
                <div className="mt-1">
                  <div>:</div>
                  <SyntaxHighlighter language="json" style={vscDarkPlus} className="text-xs mt-1 rounded">
                    {JSON.stringify(tool.input, null, 2)}
                  </SyntaxHighlighter>
                </div>
              )}
              
              {tool.result && (
                <div className="mt-2">
                  <div>: {tool.result.is_error ? '' : ''}</div>
                </div>
              )}
            </div>
          ))}
        </div>
      )}
      
      {message.sender === 'assistant' && message.artifacts && message.artifacts.length > 0 && (
        <div className="text-purple-600 mb-3">
          [ {message.artifacts.length} Artifacts]
        </div>
      )}
      
      <ReactMarkdown
        remarkPlugins={[remarkGfm]}
        components={{
          code({node, inline, className, children, ...props}) {
            const match = /language-(\w+)/.exec(className || '')
            return !inline && match ? (
              <SyntaxHighlighter
                style={vscDarkPlus}
                language={match[1]}
                PreTag="div"
                {...props}
              >{String(children).replace(/\n$/, '')}</SyntaxHighlighter>
            ) : (
              <code className={className} {...props}>
                {children}
              </code>
            )
          },
          ...customRenderers
        }}
      >
        {message.display_text}
      </ReactMarkdown>
    </div>
  );
};

// : 
const ThinkingView = ({ message }) => (
  <div>
    {message.thinking ? (
      <pre className="whitespace-pre-wrap font-mono text-sm">{message.thinking}</pre>
    ) : (
      <div className="text-gray-500"></div>
    )}
  </div>
);

// : Artifacts 
const ArtifactsView = ({ message }) => (
  <div>
    {message.artifacts && message.artifacts.length > 0 ? (
      message.artifacts.map((artifact, idx) => (
        <div key={idx} className="mb-4 pb-4 border-b border-gray-200">
          <div className="font-medium">
            Artifact {idx + 1}: {artifact.title || ''} (ID: {artifact.id || ''})
          </div>
          <div className="text-sm mt-1">: {artifact.type || ''}</div>
          <div className="border-t border-gray-200 my-2"></div>
          
          {artifact.command === 'create' && (
            <>
              {artifact.language && <div className="text-sm">: {artifact.language}</div>}
              <div className="mt-2">:</div>
              <SyntaxHighlighter
                style={vscDarkPlus}
                language={artifact.language || 'text'}
                className="text-xs mt-1 rounded"
              >
                {artifact.content || ''}
              </SyntaxHighlighter>
            </>
          )}
          
          {(artifact.command === 'update' || artifact.command === 'rewrite') && (
            <>
              <div className="text-sm">: {artifact.command}</div>
              <div className="mt-2">:</div>
              <SyntaxHighlighter
                style={vscDarkPlus}
                language="text"
                className="text-xs mt-1 rounded"
              >
                {artifact.old_str || ''}
              </SyntaxHighlighter>
              <div className="mt-2">:</div>
              <SyntaxHighlighter
                style={vscDarkPlus}
                language="text"
                className="text-xs mt-1 rounded"
              >
                {artifact.new_str || ''}
              </SyntaxHighlighter>
            </>
          )}
        </div>
      ))
    ) : (
      <div className="text-gray-500">Artifacts</div>
    )}
  </div>
);

// : JSON 
const JsonView = ({ message }) => (
  <div className="json-content">
    <SyntaxHighlighter
      style={vscDarkPlus}
      language="json"
      className="text-xs rounded"
      wrapLines={true}
      wrapLongLines={true}
    >
      {JSON.stringify(message.content_items || {}, null, 2)}
    </SyntaxHighlighter>
  </div>
);

// : 
const SearchBar = ({ 
  query, 
  setQuery, 
  onSearch, 
  onClear, 
  onNext, 
  onPrev, 
  results, 
  currentIndex, 
  isSearching,
  error,
  isCrossFileSearching, // 
  totalFiles, // 
  currentFileIndex // 
}) => (
  <div className="p-2 bg-white border-b border-gray-300">
    <div className="flex items-center justify-between">
      <div className="relative flex-1 flex">
        <input 
          type="text" 
          placeholder="..." 
          className="w-full pl-8 pr-10 py-1.5 border border-gray-300 rounded text-sm focus:ring-2 focus:ring-blue-300 focus:border-blue-500 transition-all"
          value={query}
          onChange={(e) => setQuery(e.target.value)}
          onKeyDown={(e) => {
            if (e.key === 'Enter') {
              e.preventDefault();
              if (e.shiftKey) {
                onPrev();
              } else {
                onNext();
              }
            }
          }}
        />
        <Search className="absolute left-2 top-2 h-4 w-4 text-gray-400" />
        
        {query && (
          <button
            className="absolute right-2 top-2 text-gray-400 hover:text-gray-600 focus:outline-none"
            onClick={onClear}
          >
            <X className="h-4 w-4" />
          </button>
        )}
      </div>
      
      <div className="flex ml-2">
        <button 
          className={`py-1.5 px-3 rounded-l text-sm transition-colors focus:outline-none focus:ring-2 focus:ring-offset-1 ${
            isSearching || !query.trim() 
              ? 'bg-gray-300 text-gray-500 cursor-not-allowed' 
              : 'bg-blue-500 text-white hover:bg-blue-600'
          }`}
          onClick={onSearch}
          disabled={isSearching || !query.trim()}
        >
          {isSearching ? (
            <span className="flex items-center">
              <svg className="animate-spin -ml-1 mr-2 h-4 w-4 text-white" xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 24 24">
                <circle className="opacity-25" cx="12" cy="12" r="10" stroke="currentColor" strokeWidth="4"></circle>
                <path className="opacity-75" fill="currentColor" d="M4 12a8 8 0 018-8V0C5.373 0 0 5.373 0 12h4zm2 5.291A7.962 7.962 0 014 12H0c0 3.042 1.135 5.824 3 7.938l3-2.647z"></path>
              </svg>
              
            </span>
          ) : ""}
        </button>
        <button 
          className={`py-1.5 px-2 text-sm border-l border-gray-300 ${
            results.length === 0 
              ? 'bg-gray-200 text-gray-400 cursor-not-allowed' 
              : 'bg-gray-200 text-gray-700 hover:bg-gray-300'
          }`}
          onClick={onPrev}
          disabled={results.length === 0}
          title=" (Shift+)"
        >
          <svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round">
            <path d="M18 15l-6-6-6 6"/>
          </svg>
        </button>
        <button 
          className={`py-1.5 px-2 rounded-r text-sm ${
            results.length === 0 
              ? 'bg-gray-200 text-gray-400 cursor-not-allowed' 
              : 'bg-gray-200 text-gray-700 hover:bg-gray-300'
          }`}
          onClick={onNext}
          disabled={results.length === 0}
          title=" ()"
        >
          <svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round">
            <path d="M6 9l6 6 6-6"/>
          </svg>
        </button>
      </div>
    </div>
    
    {/*  -  */}
    {(results.length > 0 || error || isCrossFileSearching) && (
      <div className="flex justify-between items-center mt-2 text-xs">
        <div>
          {results.length > 0 ? (
            <span className="search-counter bg-blue-50 text-blue-700 px-2 py-1 rounded-full border border-blue-200">
              <span className="font-bold">{currentIndex + 1}</span> / <span>{results.length}</span> 
              {totalFiles > 1 && (
                <span className="ml-2 text-gray-600">
                   {currentFileIndex + 1}/{totalFiles} 
                  {isCrossFileSearching && (
                    <span className="ml-1 text-pink-600">...</span>
                  )}
                </span>
              )}
            </span>
          ) : (
            error && <span className="text-red-500 bg-red-50 px-2 py-1 rounded-full border border-red-200">{error}</span>
          )}
        </div>
        {results.length > 0 && (
          <button 
            className="text-blue-600 hover:text-blue-800 hover:underline focus:outline-none"
            onClick={onClear}
          >
            
          </button>
        )}
      </div>
    )}
  </div>
);

// : 
const TabBar = ({ activeTab, setActiveTab, hasBranches }) => (
  <div className="bg-white border-b border-gray-300 flex">
    <TabButton 
      label="" 
      icon={<BookOpen className="h-4 w-4 mr-1" />}
      isActive={activeTab === 'content'} 
      onClick={() => setActiveTab('content')} 
    />
    <TabButton 
      label="" 
      icon={<Terminal className="h-4 w-4 mr-1" />}
      isActive={activeTab === 'thinking'} 
      onClick={() => setActiveTab('thinking')} 
    />
    <TabButton 
      label="Artifacts" 
      icon={<FileText className="h-4 w-4 mr-1" />}
      isActive={activeTab === 'artifacts'} 
      onClick={() => setActiveTab('artifacts')} 
    />
    {hasBranches && (
      <TabButton 
        label="JSON" 
        icon={<Code className="h-4 w-4 mr-1" />}
        isActive={activeTab === 'json'} 
        onClick={() => setActiveTab('json')} 
      />
    )}
  </div>
);

// : 
const TabButton = ({ label, icon, isActive, onClick }) => (
  <button 
    className={`px-4 py-2 text-sm font-medium flex items-center ${
      isActive 
        ? 'text-blue-600 border-b-2 border-blue-500' 
        : 'text-gray-500 hover:text-gray-700'
    }`}
    onClick={onClick}
  >
    {icon}
    {label}
  </button>
);

// : 
const MultiSelectView = ({ selectedMessages, activeTab }) => (
  <div className="p-4">
    <div className="font-medium text-green-600 mb-4">
       {selectedMessages.length}  
      (: {selectedMessages.filter(msg => msg.sender === 'human').length}, 
      Claude: {selectedMessages.filter(msg => msg.sender === 'assistant').length})
    </div>
    
    {activeTab === 'content' && (
      <div>
        <div className="font-medium mb-2">:</div>
        
        {selectedMessages.map((msg, idx) => (
          <div key={idx} className="mb-4 pb-4 border-b border-gray-200">
            <div className="font-medium">
              {idx + 1}. {msg.sender_label} @ {msg.timestamp}
            </div>
            {msg.sender === 'assistant' && msg.tools && msg.tools.length > 0 && (
              <div className="text-blue-600 text-sm mb-1">
                [ {msg.tools.length} : {msg.tools.map(t => t.name).join(', ')}]
              </div>
            )}
            {msg.sender === 'assistant' && msg.artifacts && msg.artifacts.length > 0 && (
              <div className="text-purple-600 text-sm mb-1">
                [ {msg.artifacts.length} Artifacts]
              </div>
            )}
            <div className="mt-1">
              <ReactMarkdown
                remarkPlugins={[remarkGfm]}
                components={{
                  code({node, inline, className, children, ...props}) {
                    const match = /language-(\w+)/.exec(className || '')
                    return !inline && match ? (
                      <SyntaxHighlighter
                        style={vscDarkPlus}
                        language={match[1]}
                        PreTag="div"
                        {...props}
                      >{String(children).replace(/\n$/, '')}</SyntaxHighlighter>
                    ) : (
                      <code className={className} {...props}>
                        {children}
                      </code>
                    )
                  }
                }}
              >
                {msg.display_text}
              </ReactMarkdown>
            </div>
          </div>
        ))}
      </div>
    )}
    
    {activeTab === 'thinking' && (
      <div>
        <div className="font-medium mb-2">:</div>
        
        {selectedMessages
          .filter(msg => msg.thinking)
          .map((msg, idx) => (
            <div key={idx} className="mb-4 pb-4 border-b border-gray-200">
              <div className="font-medium">
                {msg.sender_label} @ {msg.timestamp}
              </div>
              <pre className="whitespace-pre-wrap font-mono text-sm mt-2 bg-gray-50 p-3 rounded">{msg.thinking}</pre>
            </div>
          ))}
          
        {selectedMessages.filter(msg => msg.thinking).length === 0 && (
          <div className="text-gray-500"></div>
        )}
      </div>
    )}
    
    {activeTab === 'artifacts' && (
      <div>
        <div className="font-medium mb-2">Artifacts:</div>
        
        {selectedMessages
          .filter(msg => msg.artifacts && msg.artifacts.length > 0)
          .map((msg, msgIdx) => (
            <div key={msgIdx} className="mb-4">
              <div className="font-medium border-b border-gray-200 pb-1 mb-2">
                {msg.sender_label} @ {msg.timestamp}:
              </div>
              
              {msg.artifacts.map((artifact, artIdx) => (
                <div key={artIdx} className="mb-4 pb-4 border-b border-gray-200 pl-4">
                  <div className="font-medium">
                    Artifact {artIdx + 1}: {artifact.title || ''} (ID: {artifact.id || ''})
                  </div>
                  <div className="text-sm mt-1">: {artifact.type || ''}</div>
                  <div className="border-t border-gray-200 my-2"></div>
                  
                  {artifact.command === 'create' && (
                    <>
                      {artifact.language && <div className="text-sm">: {artifact.language}</div>}
                      <div className="mt-2">:</div>
                      <SyntaxHighlighter
                        style={vscDarkPlus}
                        language={artifact.language || 'text'}
                        className="text-xs mt-1 rounded"
                      >
                        {artifact.content || ''}
                      </SyntaxHighlighter>
                    </>
                  )}
                  
                  {(artifact.command === 'update' || artifact.command === 'rewrite') && (
                    <>
                      <div className="text-sm">: {artifact.command}</div>
                      <div className="mt-2">:</div>
                      <pre className="bg-gray-50 p-2 rounded mt-1 text-xs overflow-auto">
                        {artifact.old_str || ''}
                      </pre>
                      <div className="mt-2">:</div>
                      <pre className="bg-gray-50 p-2 rounded mt-1 text-xs overflow-auto">
                        {artifact.new_str || ''}
                      </pre>
                    </>
                  )}
                </div>
              ))}
            </div>
          ))}
          
        {selectedMessages.filter(msg => msg.artifacts && msg.artifacts.length > 0).length === 0 && (
          <div className="text-gray-500">Artifacts</div>
        )}
      </div>
    )}
    
    {activeTab === 'json' && (
      <div className="json-tab-content">
        <div className="font-medium mb-2">JSON:</div>
        
        {selectedMessages.map((msg, idx) => (
          <div key={idx} className="mb-4 pb-4 border-b border-gray-200">
            <div className="font-medium">
              {msg.sender_label} @ {msg.timestamp}:
            </div>
            <div className="json-content">
              <SyntaxHighlighter
                style={vscDarkPlus}
                language="json"
                className="text-xs mt-2 rounded"
                wrapLines={true}
                wrapLongLines={true}
              >
                {JSON.stringify(msg.content_items || {}, null, 2)}
              </SyntaxHighlighter>
            </div>
          </div>
        ))}
      </div>
    )}
  </div>
);

// CSS
const searchHighlightStyles = `
  .search-highlight {
    background-color: #fff176;
    padding: 0 2px;
    border-radius: 2px;
  }
  
  .search-highlight.active {
    background-color: #ffa000;
    color: white;
    font-weight: bold;
  }
  
  /*  */
  @keyframes pulse-border {
    0% { border-color: #3b82f6; }
    50% { border-color: #ec4899; }
    100% { border-color: #3b82f6; }
  }
  
  .cross-file-searching {
    animation: pulse-border 1.5s infinite;
  }
`;

// 
const MessageDetail = ({
  processedData,
  selectedMessageIndices,
  activeTab,
  setActiveTab,
  contentSearchQuery,
  setContentSearchQuery,
  onHighlightCreate = () => {},
  // 
  files = [],                   
  currentFileIndex = 0,         
  handleSelectFile = () => {}  
}) => {
  const contentContainerRef = useRef(null);
  const thinkingContainerRef = useRef(null);
  const artifactsContainerRef = useRef(null);
  const jsonContainerRef = useRef(null);
  
  // 
  const [searchResults, setSearchResults] = useState([]);
  const [currentResultIndex, setCurrentResultIndex] = useState(-1);
  const [searchError, setSearchError] = useState("");
  const [isSearching, setIsSearching] = useState(false);
  const [highlightedNodes, setHighlightedNodes] = useState([]); // 
  const [isSearchingAcrossFiles, setIsSearchingAcrossFiles] = useState(false);
  const [isCrossFileSearching, setIsCrossFileSearching] = useState(false); // 
  
  // 
  useEffect(() => {
    // 
    const styleElement = document.createElement('style');
    styleElement.textContent = searchHighlightStyles;
    document.head.appendChild(styleElement);
    
    // 
    return () => {
      document.head.removeChild(styleElement);
    };
  }, []);
  
  // 
  const getCurrentContainerRef = () => {
    switch (activeTab) {
      case 'content': return contentContainerRef;
      case 'thinking': return thinkingContainerRef;
      case 'artifacts': return artifactsContainerRef;
      case 'json': return jsonContainerRef;
      default: return contentContainerRef;
    }
  };
  
  // 
  const hasBranches = processedData && Array.isArray(processedData.branches) && processedData.branches.length > 0;
  
  // 
  useEffect(() => {
    if (contentSearchQuery === '') {
      setSearchResults([]);
      setCurrentResultIndex(-1);
      setSearchError("");
      clearHighlights();
    }
  }, [contentSearchQuery]);
  
  // contentSearchQuery
  useEffect(() => {
    if (contentSearchQuery && contentSearchQuery.trim() !== '') {
      // 
      const timer = setTimeout(() => {
        handleSearchInContent();
      }, 300);
      
      return () => clearTimeout(timer);
    }
  }, [contentSearchQuery, activeTab, selectedMessageIndices]);
  
  // 
  useEffect(() => {
    clearHighlights();
    
    // 
    if (contentSearchQuery && contentSearchQuery.trim() !== '') {
      setTimeout(() => {
        handleSearchInContent();
      }, 300);
    }
  }, [activeTab]);
  
  // 
  const clearHighlights = () => {
    // 
    const containerRef = getCurrentContainerRef();
    if (containerRef.current) {
      const highlights = containerRef.current.querySelectorAll('.search-highlight');
      highlights.forEach(el => {
        const textContent = el.textContent;
        el.outerHTML = textContent;
      });
    }
    
    setHighlightedNodes([]);
    setSearchResults([]);
    setCurrentResultIndex(-1);
  };

  // 
  const handleSearchInContent = () => {
    if (!contentSearchQuery.trim()) {
      clearHighlights();
      return;
    }
    
    setIsSearching(true);
    setSearchError("");
    
    try {
      // 
      const containerRef = getCurrentContainerRef();
      if (!containerRef.current) {
        setSearchError("");
        setIsSearching(false);
        return;
      }
      
      // 
      clearHighlights();
      
      const query = contentSearchQuery.toLowerCase();
      
      // DOM
      const tempContainer = document.createElement('div');
      tempContainer.innerHTML = containerRef.current.innerHTML;
      
      // 
      const allHighlightNodes = highlightAllMatches(tempContainer, query);
      
      // DOM
      containerRef.current.innerHTML = tempContainer.innerHTML;
      
      // DOM
      const highlights = containerRef.current.querySelectorAll('.search-highlight');
      const highlightResults = Array.from(highlights).map((el, index) => ({
        node: el,
        position: index
      }));
      
      setHighlightedNodes(highlightResults);
      setSearchResults(highlightResults);
      
      if (highlightResults.length > 0) {
        // 
        setCurrentResultIndex(0);
        highlightResults[0].node.classList.add('active');
        
        // 
        setTimeout(() => {
          if (highlightResults[0].node) {
            highlightResults[0].node.scrollIntoView({
              behavior: 'smooth',
              block: 'center'
            });
          }
        }, 100);
      } else {
        setSearchError(` "${contentSearchQuery}"`);
      }
    } catch (error) {
      console.error(":", error);
      setSearchError(": " + error.message);
    } finally {
      setIsSearching(false);
    }
  };
  
  // 
  function highlightAllMatches(rootNode, searchText) {
    const nodeStack = [rootNode];
    let allHighlightNodes = [];
    
    while (nodeStack.length > 0) {
      const currentNode = nodeStack.pop();
      
      if (!currentNode) continue;
      
      // 
      if (
        currentNode.nodeName === 'SCRIPT' || 
        currentNode.nodeName === 'STYLE' || 
        currentNode.nodeName === 'PRE' || 
        currentNode.nodeName === 'CODE'
      ) {
        continue;
      }
      
      // 
      if (currentNode.nodeType === Node.TEXT_NODE) {
        let textNode = currentNode;
        let nodeText = textNode.nodeValue;
        let lowerNodeText = nodeText.toLowerCase();
        let startIndex = 0;
        let index;
        
        // 
        while ((index = lowerNodeText.indexOf(searchText, startIndex)) !== -1) {
          // 
          const beforeText = nodeText.substring(0, index);
          const matchText = nodeText.substring(index, index + searchText.length);
          const afterText = nodeText.substring(index + searchText.length);
          
          // 
          const beforeNode = document.createTextNode(beforeText);
          const highlightNode = document.createElement('span');
          highlightNode.className = 'search-highlight';
          highlightNode.textContent = matchText;
          const afterNode = document.createTextNode(afterText);
          
          // 
          const parent = textNode.parentNode;
          parent.insertBefore(beforeNode, textNode);
          parent.insertBefore(highlightNode, textNode);
          parent.replaceChild(afterNode, textNode);
          
          // 
          allHighlightNodes.push(highlightNode);
          
          // 
          textNode = afterNode;
          nodeText = afterText;
          lowerNodeText = nodeText.toLowerCase();
          startIndex = 0;
          
          // 
          if (lowerNodeText.length < searchText.length) {
            break;
          }
        }
      }
      // 
      else if (currentNode.nodeType === Node.ELEMENT_NODE) {
        // 
        for (let i = currentNode.childNodes.length - 1; i >= 0; i--) {
          nodeStack.push(currentNode.childNodes[i]);
        }
      }
    }
    
    return allHighlightNodes;
  }

  // 
  const crossFileSearch = (direction = 'next') => {
    // 
    if (!files || files.length <= 1) {
      setSearchError("");
      return;
    }
    
    // 
    let nextFileIndex;
    if (direction === 'next') {
      nextFileIndex = currentFileIndex < files.length - 1 ? currentFileIndex + 1 : 0;
    } else {
      nextFileIndex = currentFileIndex > 0 ? currentFileIndex - 1 : files.length - 1;
    }
    
    // 
    setIsSearching(true);
    setIsCrossFileSearching(true); // 
    setSearchError(`${direction === 'next' ? '' : ''}...`);
    
    // 
    setTimeout(() => {
      // 
      const currentQuery = contentSearchQuery;
      
      // 
      setSearchResults([]);
      setCurrentResultIndex(-1);
      
      // 
      handleSelectFile(nextFileIndex);
      
      // 
      localStorage.setItem('autoSearchAfterLoad', currentQuery);
      localStorage.setItem('searchDirection', direction);
      
      // 
      setTimeout(() => {
        setIsSearching(false);
        setIsCrossFileSearching(false); // 
      }, 500);
    }, 300);
  };

  // 
  useEffect(() => {
    // 
    const autoSearchQuery = localStorage.getItem('autoSearchAfterLoad');
    if (autoSearchQuery) {
      // 
      localStorage.removeItem('autoSearchAfterLoad');
      
      // 
      const searchDirection = localStorage.getItem('searchDirection');
      localStorage.removeItem('searchDirection');
      
      // 
      setContentSearchQuery(autoSearchQuery);
      
      // DOM
      setTimeout(() => {
        handleSearchInContent();
        
        // 
        setSearchError(`${searchDirection === 'next' ? '' : ''} "${autoSearchQuery}"`);
        
        // 
        setTimeout(() => {
          setSearchError('');
        }, 3000);
      }, 500);
    }
  }, [processedData]); // 

  // 
  const jumpToNextResult = () => {
    // 
    if (searchResults.length === 0 && contentSearchQuery.trim()) {
      handleSearchInContent();
      return;
    }
    
    try {
      // 
      if (searchResults.length > 0) {
        // 
        if (currentResultIndex >= 0 && currentResultIndex < searchResults.length) {
          const currentNode = searchResults[currentResultIndex].node;
          if (currentNode && currentNode.classList) {
            currentNode.classList.remove('active');
          }
        }
        
        // 
        if (currentResultIndex < searchResults.length - 1) {
          // 
          const nextIndex = currentResultIndex + 1;
          setCurrentResultIndex(nextIndex);
          
          // 
          const nextNode = searchResults[nextIndex].node;
          if (nextNode && nextNode.classList) {
            nextNode.classList.add('active');
            
            // 
            setTimeout(() => {
              try {
                if (nextNode.scrollIntoView) {
                  nextNode.scrollIntoView({
                    behavior: 'smooth',
                    block: 'center'
                  });
                }
              } catch (scrollError) {
                console.error(":", scrollError);
                // 
                const container = getCurrentContainerRef().current;
                if (container) {
                  container.scrollTop = nextNode.offsetTop - (container.clientHeight / 2);
                }
              }
            }, 0);
          }
          
          return; // 
        }
        // 
        else if (files.length > 1 && currentFileIndex < files.length - 1) {
          // 
          crossFileSearch('next');
          return; // 
        }
      }
      
      // 
      if (searchResults.length > 0) {
        // 
        setCurrentResultIndex(0);
        const firstNode = searchResults[0].node;
        if (firstNode && firstNode.classList) {
          firstNode.classList.add('active');
          
          // 
          setTimeout(() => {
            try {
              if (firstNode.scrollIntoView) {
                firstNode.scrollIntoView({
                  behavior: 'smooth',
                  block: 'center'
                });
              }
            } catch (scrollError) {
              console.error(":", scrollError);
            }
          }, 0);
        }
      } else {
        // 
        handleSearchInContent();
      }
    } catch (error) {
      console.error(":", error);
      // 
      handleSearchInContent();
    }
  };
  
  // 
  const jumpToPrevResult = () => {
    // 
    if (searchResults.length === 0 && contentSearchQuery.trim()) {
      handleSearchInContent();
      return;
    }
    
    try {
      // 
      if (searchResults.length > 0) {
        // 
        if (currentResultIndex >= 0 && currentResultIndex < searchResults.length) {
          const currentNode = searchResults[currentResultIndex].node;
          if (currentNode && currentNode.classList) {
            currentNode.classList.remove('active');
          }
        }
        
        // 
        if (currentResultIndex > 0) {
          // 
          const prevIndex = currentResultIndex - 1;
          setCurrentResultIndex(prevIndex);
          
          // 
          const prevNode = searchResults[prevIndex].node;
          if (prevNode && prevNode.classList) {
            prevNode.classList.add('active');
            
            // 
            setTimeout(() => {
              try {
                if (prevNode.scrollIntoView) {
                  prevNode.scrollIntoView({
                    behavior: 'smooth',
                    block: 'center'
                  });
                }
              } catch (scrollError) {
                console.error(":", scrollError);
                // 
                const container = getCurrentContainerRef().current;
                if (container) {
                  container.scrollTop = prevNode.offsetTop - (container.clientHeight / 2);
                }
              }
            }, 0);
          }
          
          return; // 
        }
        // 
        else if (files.length > 1 && currentFileIndex > 0) {
          // 
          crossFileSearch('prev');
          return; // 
        }
      }
      
      // 
      if (searchResults.length > 0) {
        // 
        const lastIndex = searchResults.length - 1;
        setCurrentResultIndex(lastIndex);
        const lastNode = searchResults[lastIndex].node;
        if (lastNode && lastNode.classList) {
          lastNode.classList.add('active');
          
          // 
          setTimeout(() => {
            try {
              if (lastNode.scrollIntoView) {
                lastNode.scrollIntoView({
                  behavior: 'smooth',
                  block: 'center'
                });
              }
            } catch (scrollError) {
              console.error(":", scrollError);
            }
          }, 0);
        }
      } else {
        // 
        handleSearchInContent();
      }
    } catch (error) {
      console.error(":", error);
      // 
      handleSearchInContent();
    }
  };

  // 
  const clearSearch = () => {
    setContentSearchQuery('');
    clearHighlights();
  };

  // 
  useEffect(() => {
    const handleKeyDown = (event) => {
      // 
      if (!document.activeElement || 
          document.activeElement.tagName === 'BODY' || 
          document.activeElement.tagName === 'DIV') {
        
        // F3  Ctrl+G
        if (event.key === 'F3' || (event.ctrlKey && event.key === 'g')) {
          event.preventDefault();
          jumpToNextResult();
        }
        // Shift+F3  Ctrl+Shift+G
        else if ((event.shiftKey && event.key === 'F3') || 
                 (event.ctrlKey && event.shiftKey && event.key === 'g')) {
          event.preventDefault();
          jumpToPrevResult();
        }
        // Escape
        else if (event.key === 'Escape' && contentSearchQuery) {
          event.preventDefault();
          clearSearch();
        }
      }
    };
    
    window.addEventListener('keydown', handleKeyDown);
    return () => window.removeEventListener('keydown', handleKeyDown);
  }, [contentSearchQuery, searchResults, currentResultIndex]);
  
  // UI
  const renderSearchBar = () => (
    <SearchBar
      query={contentSearchQuery}
      setQuery={setContentSearchQuery}
      onSearch={handleSearchInContent}
      onClear={clearSearch}
      onNext={jumpToNextResult}
      onPrev={jumpToPrevResult}
      results={searchResults}
      currentIndex={currentResultIndex}
      isSearching={isSearching}
      error={isSearchingAcrossFiles ? "..." : searchError}
      isCrossFileSearching={isCrossFileSearching}
      totalFiles={files?.length || 1}
      currentFileIndex={currentFileIndex}
    />
  );

  // 
  const renderMessageDetails = () => {
    if (selectedMessageIndices.length === 0 || !processedData) {
      return <div className="p-4 text-gray-500"></div>;
    }

    // 
    if (selectedMessageIndices.length > 1) {
      // 
      const selectedMessages = selectedMessageIndices
        .map(index => processedData.chat_history.find(msg => msg.index === index))
        .filter(Boolean);

      return (
        <div className="detail-content-container" ref={contentContainerRef}>
          <MultiSelectView 
            selectedMessages={selectedMessages}
            activeTab={activeTab}
          />
        </div>
      );
    }

    // 
    const messageIndex = selectedMessageIndices[0];
    const message = processedData.chat_history.find(msg => msg.index === messageIndex);
    
    if (!message) return <div className="p-4 text-gray-500"></div>;
    
    switch (activeTab) {
      case 'content':
        return (
          <div className="p-4 detail-content-container" ref={contentContainerRef}>
            <ContentView message={message} searchQuery={contentSearchQuery} onHighlight={onHighlightCreate} />
          </div>
        );
      case 'thinking':
        return (
          <div className="p-4 detail-content-container" ref={thinkingContainerRef}>
            <ThinkingView message={message} />
          </div>
        );
      case 'artifacts':
        return (
          <div className="p-4 detail-content-container" ref={artifactsContainerRef}>
            <ArtifactsView message={message} />
          </div>
        );
      case 'json':
        return hasBranches ? (
          <div className="p-4 detail-content-container json-tab-content" ref={jsonContainerRef}>
            <JsonView message={message} />
          </div>
        ) : null;
      default:
        return null;
    }
  };

  return (
    <div className="flex-1 flex flex-col h-full overflow-visible">
      {/*  */}
      <div className="flex-1 flex flex-col h-full overflow-visible">
        {/*  */}
        <TabBar 
          activeTab={activeTab} 
          setActiveTab={setActiveTab} 
          hasBranches={hasBranches} 
        />

        {/*  -  */}
        {renderSearchBar()}

        {/*  */}
        <div className={`flex-1 bg-white h-full overflow-visible pb-8 ${isCrossFileSearching ? 'border-2 border-blue-500 cross-file-searching' : ''}`}>
          {renderMessageDetails()}
        </div>
      </div>
    </div>
  );
};

export default MessageDetail;